
import Vue from 'vue';
import { email, required, minLength } from 'vuelidate/lib/validators';

export default Vue.extend({
  name: 'LoginPage',
  data: () => ({
    valid: true,
    lazy: false,

    email: '',
    emailRules: [
      (v: string) => !!v || 'E-mail  ',
      (v: string) => /.+@.+\..+/.test(v) || 'E-mail   ',
    ],

    password: '',
    passwordShow: false,
    passwordLength: 4,
    passwordRules: [
      (v: string) => !!v || 'Password  ',
      (v: string) =>
        // TODO:    passwordLength
        (v && v.length >= 4) ||
        `     ${4} `,
    ],

    checkbox: false,
    checkboxRules: [(v: string) => !!v || '   !'],
  }),

  methods: {
    onSubmit() {
      console.log('onSubmit ');
      this.$router.push('/');
    },
  },
});
